.trust-block {
  padding: 60px 0;

  @media screen and (min-width: 1152px) {
    padding: 120px 0;
  }

  .container {
    display: flex;
    flex-wrap: wrap;
  }

  &__title {
    margin-bottom: 24px;
    width: 100%;
  }

  &__filter {
    display: flex;
    overflow-x: auto;
    width: calc(100% + 20px);
    margin: -10px -10px 50px;

    &-item {
      margin: 10px;
      flex-shrink: 0;

      &.is-active {
        background: var(--color-accent-hover);
        color: #fff;
      }
    }
  }

  .splide__track {
    width: 100%;
  }

  .splide__slide {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
      -o-object-fit: contain;
      object-fit: contain;
      -o-object-position: center;
      object-position: center;
    }
  }

  &__arrows {
    width: 100%;
    padding-top: 48px;
  }

  &__sliders {
    width: 100%;

    .splide {
      display: none;

      &.is-visible {
        display: block;
      }
    }

    & ul {

      & li {
        & picture {
          padding-top: 30%;
          position: relative;
          display: block;
          width: 100%;

          & img {
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            max-width: 80%;
            max-height: 90%;
            margin: auto;
          }
        }
      }
    }

  }

  @media screen and (min-width: 831px) {
    padding: 100px 0;

    .container {
      align-items: center;
    }

    &__filter {
      margin-top: 0;
      margin-bottom: 0;
      margin-right: auto;
      flex-wrap: wrap;
    }

    &__arrows {
      width: auto;

      .splide__arrow--next {
        max-width: 160px;
      }
    }

    .splide__track {
      padding-top: 48px;
      width: 100%;
    }
  }

  @media screen and (min-width: 1025px) {
    &__title {
      margin-bottom: 48px;
    }

    &__filter {
      overflow-x: hidden;
      flex-wrap: wrap;
    }
  }
}

.trust-block+.certificates-block {
  padding-top: 0;
}